.DashBoardBodyCommon
{
    display: table;
    overflow: auto;
    width: 100%;
    height: 100%;
}

#DashboardAdmin
{
    background-color: white;
    background-image: url("../../media/Arts/dashboard-admin-mobile.jpg");
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat; 
}

#DashboardStaff
{
    background-color: white;
    background-image: url("../../media/Arts/dashboard-staff-mobile.jpg");
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat; 
}

#DashboardMember
{
    background-color: white;
    background-image: url("../../media/Arts/dashboard-member-mobile.jpg");
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat; 
}

.ContentHeading
{
    font-size: 2em;
    font-weight: bolder;
    padding-left: 10px;
    padding-top: 10px;
    color: rgb(0, 0, 0);
}

.DashboardContentCommon
{
    display: table-row;
    text-align: center;
    height: 100%;
    padding-top: 200px;
}

.DashboardPalette
{
    margin: 20px 10px 20px 10px;
    padding: 0px 10px 0px 10px;
    border-radius: 10px;
}

#OrgSetupPalette
{
    text-align: center;  
    background-image: linear-gradient(to right, #870000 0%, #190A05  51%, #870000  100%);
    background-size: 200%;
}

#StaffPalette
{
    text-align: left;
    background: linear-gradient(90deg, #4b6cb7 0%, #182848 100%);
}

#MemberPalette
{
    text-align: left;
    background: linear-gradient(90deg, #4b6cb7 0%, #182848 100%);
}

#StaffList table
{
    width: 100%;
    background-color: rgb(93, 5, 134);
    border-radius: 5px;
    margin: 5px;
}

#StaffList thead td
{
    color: white;
    background-color: teal;
    text-align: center;
}

.ColStaffName
{
    width: 60%;
    color:rgb(255, 255, 255);
}

.ColStaffEmail
{
    width: 40%;
    color:white;
}

.PaletteVisible
{
    display: block;
}

.PaletteHidden
{
    display: none;
}

.DashboardSubheading1
{
    color: white;
    font-size: 1.5em;
    font-weight: bold;
}

.DashboardSubheading2
{
    color: rgb(255, 255, 255);
    font-size: 1em;
}

@media (prefers-color-scheme: dark) {
    #DashboardAdmin
    {
        background-color: rgb(25, 25, 25);
        background-image: url("../../media/Arts/dashboard-admin-mobile-dark.jpg");
    }

    #DashboardStaff
    {
        background-color: rgb(25, 25, 25);
        background-image: url("../../media/Arts/dashboard-staff-mobile-dark.jpg");
    }

    #DashboardMember
    {
        background-color: rgb(25, 25, 25);
        background-image: url("../../media/Arts/dashboard-member-mobile-dark.jpg");
    }

    .ContentHeading
    {
        color: white;
    }
}

@media only screen and (min-width: 768px)
{
    #DashboardAdmin
    {
        background-color: white;
        background-image: url("../../media/Arts/dashboard-admin-desktop.jpg");
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat; 
    }

    #DashboardStaff
    {
        background-color: white;
        background-image: url("../../media/Arts/dashboard-staff-desktop.jpg");
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat; 
    }

    #DashboardMember
    {
        background-color: white;
        background-image: url("../../media/Arts/dashboard-member-desktop.jpg");
        background-position: bottom;
        background-size: contain;
        background-repeat: no-repeat; 
    }

    .ContentHeading
    {
        font-size: 3em;
    }

    @media (prefers-color-scheme: dark) {
        #DashboardAdmin
        {
            background-color: rgb(25, 25, 25);
            background-image: url("../../media/Arts/dashboard-admin-desktop-dark.jpg");
        }

        #DashboardStaff
        {
            background-color: rgb(25, 25, 25);
            background-image: url("../../media/Arts/dashboard-staff-desktop-dark.jpg");
        }

        #DashboardMember
        {
            background-color: rgb(25, 25, 25);
            background-image: url("../../media/Arts/dashboard-member-desktop-dark.jpg");
        }
    }
}